import axios from 'axios'
import  NProgress from 'nprogress/nprogress'
import {Message}  from "@arco-design/web-vue";
import store from "@/store";


axios.defaults.baseURL = process.env.BASE_URL
// axios.defaults.headers.token = store.getters.getcur_User.token
// axios.defaults.headers.userid = store.getters.getcur_User.user_id

axios.interceptors.request.use(config => {
    NProgress.start()
    config.headers.setAuthorization(store.getters.getuserInfo.token!==undefined?store.getters.getuserInfo.token:null)
    // console.log(config)
    // config.headers['token'] = ref(store.getters.getcur_User.token)
    // console.log(config.headers)
    return config
}, error => {
    return Promise.reject(error)
},{
    synchronous:true
})

axios.interceptors.response.use(response => response, error => Promise.resolve(error.response))

