function checkCode(res, errMsg) {
    console.log(errMsg)
    if (!res.data.status) {
        switch (res.data.code) {
            case 2000:
                message.success(res.data.message || '未知异常')
                break
            case -404:
                message.error(res.data.message || '未知异常')
                break
            case -500:
                message.error('服务器连接错误！')
                break
            case 70005:
                message.error('您需要重新登录！')
                break
            // default:
            //     errMsg ? ElMessage.error(errMsg) : ElMessage.error(res.data.ElMessage || '未知异常')
        }
    }
    return res.data
}

export default {
    POST(url, data) {
        // console.log(errMsg)
        // const CancelToken = axios.CancelToken
        return axios.post(url, data, {
            timeout: 30000,
            // cancelToken: new CancelToken(function executor (c) {
            //
            // })
        }).then(checkStatus)
        // .then(res => checkCode(res, errMsg))
    },
    GET(url, params, errMsg) {
        // console.log(errMsg)
        // const CancelToken = axios.CancelToken
        return axios.get(url, {
            params: {
                ...params
            },
            timeout: 30000,
            // cancelToken: new CancelToken(function executor (c) {
            //
            // })
        }).then(checkStatus)
        // .then(res => checkCode(res, errMsg))
    },
    DELETE(url, errMsg) {
        // const CancelToken = axios.CancelToken
        return axios.delete(url, {
            timeout: 30000,
            // cancelToken: new CancelToken(function executor () {
            //
            // })
        }).then(checkStatus).then(res => checkCode(res, errMsg))
    },
    PUT(url, data, errMsg) {
        console.log(errMsg)
        // const CancelToken = axios.CancelToken
        return axios.put(url, data, {
            timeout: 30000,
            // cancelToken: new CancelToken(function executor () {
            //
            // })
        }).then(checkStatus).then(res => checkCode(res, errMsg))
    },
    EXPORT(url, data, errMsg) {
        console.log(errMsg)
        console.log(errMsg)
        // const CancelToken = axios.CancelToken
        return axios.post(url, data, {
            responseType: 'blob',
            timeout: 30000,
            // cancelToken: new CancelToken(function executor () {
            //
            // })
        }).then(checkStatus).then(res => res.data)
    }
}
