import {createStore} from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import {shallowRef, markRaw, defineAsyncComponent, defineComponent} from "vue";
import {getMenu} from "@/api/api";
import MainLayout from "@/layout/MainLayout";
import router from "@/router";
import NotFound from "@/views/NotFound";

const store  =createStore({
    devtools:true,
    plugins:[
        createPersistedState({storage: window.sessionStorage, key:"userInfo" })
    ],
    state: {
        userInfo: {
            token:undefined
        },
        routes:markRaw([])
    },
    getters: {
        getuserInfo:(state)=>{
            return state.userInfo
        },
        getRoutes:(state)=>{
            return state.routes
        }
    },
    mutations: {
        setuserInfo:((state, payload) => {
            state.userInfo =payload
        }),

        cleanUserInfo:((state, payload) => {
            window.sessionStorage.clear()
            state.userInfo = {
                token:undefined
            }
        }),
        setRoutes:((state, payload) => {
            state.routes =payload
            state.routes.forEach(value => {
                router.addRoute(value)
            })
        }),
        cleanRoutes:((state, payload) => {
            window.sessionStorage.clear()
            state.routes =markRaw([])
        })
    },
    actions: {}
})
export default store
