<template>
  <a-layout class="layout-demo "  style="height: 100vh;
    /*background:url('https://www.icloud.com/system/icloud.com/current/static/wallpaper.webp') no-repeat center;*/
    background-size: cover; background-color:rgba(227,227,227,0.47) !important;" >
    <a-layout-header class=" fixed-top bg-white border-bottom d-flex justify-content-between align-items-center" >
      <div class="p-3  brand">
        <span class="logo bg-secondary px-3 py-2 m-3" style="width: 10px"/>
        <!--        <span class="h6"> Teacher </span>-->
      </div>
      <a-dropdown  class="" hide-on-select trigger="hover" @select="select">
        <div :style="{cursor: 'pointer' }" class="d-flex m-2 align-items-center bg-light py-1 px-2 me-md-4 ">
          <a-avatar :size="25" :style="{ backgroundColor: '#3370ff', }" class="me-2 p-3" shape="square">
            <icon-settings/>
          </a-avatar>
          <small class="small">Settings</small>
        </div>
        <template #content style="z-index: 1000">
          <a-doption key="0_0" class="px-4 py-1">
            <template #icon>
              <icon-settings/>
            </template>
            账号设置
          </a-doption>

          <a-doption key="0_0" class="px-4 py-1">
            <template #icon>
              <icon-export/>
            </template>
            退出
          </a-doption>

        </template>
      </a-dropdown>
    </a-layout-header>

    <a-layout style="height:calc(100vh - 43px); background-color:#fff;">
      <a-layout-sider collapsible :width="220" breakpoint="md" :collapsed-width="55.5" class=" border-0 shadow-none border-end" style=" height:inherit;min-height: fit-content; width: auto; ">
        <a-menu :width="220" breakpoint="md" :collapsed-width="55.5"  :defaultSelectedKeys="defaultSelectedKey || ['dashboard']" :selected-keys="defaultSelectedKey"
                class=" shadow-none border-0 centered"  accordion   mode="vertical">

          <template v-for="link in adminSideBarLinks">
            <!--@click="onClickMenuItem(link)" -->
            <a-menu-item v-if="link.subItems.length === 0" :key="link.key"
                         @click="onClickMenuItem(link)">
              <template #icon>
                <component :is="link.icon"/>
              </template>
              <!--            <router-link :to="{name:'dashboard'}" class="position-absolute start-0 top-0 bottom-0 end-0"-->
              <!--                         @click="onClickMenuItem(link)"-->
              <!--            ></router-link>-->
              {{ link.title }}
            </a-menu-item>

            <!--  THE LINKS ON THE SIDEBAR WHICH HAVE SUB-ELEMENTS-->
            <a-sub-menu v-else :key="link.key" :selectable="true" :title="link.title">
              <template #icon>
                <component :is="link.icon"/>
              </template>
              <a-menu-item v-for="item in link.subItems" :key="item.key"
                           @click="onClickMenuItem(item)">
                {{ item.title }}
                <template #icon class="fw-bolder">
                  <component :is="item.icon" />
                </template>
                <!--              <router-link :to="{name:'dashboard'}" class="position-absolute start-0 top-0 bottom-0 end-0"-->
                <!--                           @click="onClickMenuItem(item)"-->
                <!--              ></router-link>-->
              </a-menu-item>

              <!--  END-->
            </a-sub-menu>
          </template>
        </a-menu>
      </a-layout-sider>
      <a-layout-content class="px-1 pt-1 bg-white centered">
        <router-view v-slot="{ Component }">
          <keep-alive>
            <transition mode="out-in" name="fade">
              <Suspense >
                <div class="bg-white" style="min-height: calc(100vh - 100px) !important; height: 100%">
                  <component class="" :is="Component"/>
                </div>
                <template #fallback>
                  <a-spin   class="position-absolute top-0 start-0 end-0 bottom-0"/>
                </template>
              </Suspense>
            </transition>
          </keep-alive>
        </router-view>
      </a-layout-content>
    </a-layout>

    <a-layout-footer class=" bg-transparent border d-flex justify-content-center align-items-center p-2">
      <span class="text-muted small m-0">Liu Xue Sheng Jiao Wu Guan li Xi Tong</span>
    </a-layout-footer>
  </a-layout>
</template>

<script>
import {shallowRef} from "vue";
import router from "../router";
import {Message} from "@arco-design/web-vue";

export default {
  name: "MainLayout",
  data() {
    return {
      defaultSelectedKey: ['dashboard'],
      collapsed: shallowRef(false),
      adminSideBarLinks: shallowRef([]),
    }
  },
  // props:['links'],
  methods: {
    select(item){
      Message.info(item)
      router.push({name:'home'})
    },


    changeLinkAndTab(key) {
      // console.log(this.adminSideBarLinks)

      // let filter = this.adminSideBarLinks.filter((value) => {
      //   return value.subItems.filter((item)=>item.key.toString().includes(key)).length === 1
      // });
      // console.log("KEY----------",key)
      // console.log("FILTER --------------",filter)
      // console.log("ROUTER LINK NAME: ",router.currentRoute.value.name)
      //

      this.defaultSelectedKey.pop();
      this.defaultSelectedKey = this.defaultSelectedKey.concat(key);

    },
    handleCheckTabExists(keys) {
      return this.adminSideBarLinks.filter((value) => value.key === keys).length
    },
    onClickMenuItem(link) {
      //OPEN THE CORRESPONDED TAB AND CHANGE THE SELECTED KEY


      // (this.handleCheckTabExists(link.key) === 0) ?
      //     this.changeLinkAndTab('NotFound'):
      this.changeLinkAndTab(link.key)
      router.push({name: link.key})
    },

  },
  created() {
    //ADDING THE ROUTES OPTIONS
    router.currentRoute.value.meta.links.forEach((value) => {
      this.adminSideBarLinks.push(value)
    })
    // console.log("ROUTER LINK NAME: ",router.currentRoute.value.name)
    this.changeLinkAndTab(router.currentRoute.value.name)
  },
  mounted() {
    document.body.scrollTop = 0; // For Safari
    document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
  },
  updated() {
    // console.log(this.adminSideBarLinks)
    // console.log(router.currentRoute.value.name)
    // this.onClickMenuItem({key:router.currentRoute.value.name})
    document.body.scrollTop = 0; // For Safari
    document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
  }
}
</script>

<style  lang="scss">
.arco-tabs-nav-type-capsule .arco-tabs-nav-tab:not(.arco-tabs-nav-tab-scroll)  {
  justify-content: flex-start !important;
}

.arco-tabs-nav-type-capsule .arco-tabs-nav-tab-list{
  background-color:white !important;

  & .arco-tabs-tab-active{
    //color: red !important;
    //border-top: 1px solid blue;
    border-bottom: 1px solid blue;
    background-color: rgb(47, 92, 205) !important;
    color:white !important;
    //transition: all ease 0ms;
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}


.centered{
  margin-top: 57px;
}
.arco-menu-selected {
  font-weight: bold !important;
  //font-size: small !important;
  //padding: 15px 15px !important;
}

.arco-menu-title{
  padding-right: 10px;
}
//.arco-menu-inner,.arco-menu-overflow-wrap {
//  height: 64px !important;
//  padding: 0 !important;
//  display: flex;
//  align-items: center;
//}

.layout2{
  min-height: calc(100vh - 200px) !important;
  height: fit-content;
}

.arco-tabs-nav-vertical {
  width: 200px;

  & .arco-tabs-nav-tab{
    width: inherit;
  }
}
</style>