<template>
  <div class=" mb-4 pt-0">
    <a-page-header  @back="pressback" :show-back="backbutton === true"  class="border-bottom">
      <template #title>
        <h6 class="m-0 p-0 fw-bold">
          <slot  name="icon" />
          {{text}}
        </h6>
      </template>
      <template #subtitle>
        <slot name="subtitle" />
      </template>
      <template #extra>
        <slot name="extra" />
      </template>
    </a-page-header>
  </div>
</template>

<script>
import router from "@/router";

export default {
  name: "MyPageHeader",
  props:['icon','text','backbutton'],
  methods:{
    pressback(){
      router.back()
    }
  }
}
</script>

