<template>
    <section>
      <a-table table-layout-fixed :pagination="{}" :bordered="true" show-header hoverable :columns="columns"
               :data="studentlistData" >
        <template #name="{ rowIndex,item }">

          <a-avatar :style="{ backgroundColor: '#14a9f8',marginRight:'10px' }" :size="30" shape="square">{{studentlistData[rowIndex].name[0]}}</a-avatar>
          <a-typography-text >{{studentlistData[rowIndex].name}}</a-typography-text>

        </template>
        <template #operation="{ rowIndex }">

          <!-- NEW STUDENT OPERATIONS -->
          <a-button-group v-if="operation === 1"  type="text" size="small"  >
            <a-button  @click="drawerBoolean =true" status="normal" >查看</a-button>
            <a-button  @click="modalViewBoolean =true" status="success" >分配到班级</a-button>
          </a-button-group>

          <!-- CURRENT STUDENT OPERATIONS -->
          <a-button-group v-if="operation === 2" type="text" size="small"  >
            <a-button  @click="drawerBoolean =true" status="normal" >查看</a-button>
            <a-button  @click="modalViewBoolean =true" status="warning" >请假</a-button>
            <a-button  @click="modalViewBoolean =true" status="danger" >开出</a-button>
          </a-button-group>

          <!-- ASKED FOR LEAVE STUDENT OPERATIONS -->
          <a-button-group v-if="operation === 3" type="text" size="small"  >
            <a-button  @click="drawerBoolean =true" status="normal" >查看</a-button>
            <a-button  @click="modalViewBoolean =true" status="success" >复课</a-button>
          </a-button-group>

          <!-- GRADUATED STUDENT OPERATIONS -->
          <a-button-group v-if="operation === 4" type="text" size="small"  >
            <a-button  @click="drawerBoolean =true" status="normal" >查看</a-button>
          </a-button-group>

          <!-- EXPELLED STUDENT OPERATIONS -->
          <a-button-group v-if="operation === 5" type="text" size="small"  >
            <a-button  @click="drawerBoolean =true" status="normal" >查看</a-button>
          </a-button-group>
        </template>
      </a-table>


      <a-modal :visible="modalViewBoolean" title="数统学院2022-2023-1专业课（20、21级学生）补考安排"
               @ok="load_Data_By_Direction"   unmount-on-close :width="700"
               :closable="false" @cancel="load_Data_By_Direction"
               esc-to-close mask  mask-closable
               title-align="center"   ref="modalRef" >
        <div>
          Content has to be here
        </div>
      </a-modal>
      <a-drawer unmount-on-close @ok="drawerBoolean =false" @cancel="drawerBoolean =false"
                :visible="drawerBoolean"  width="50%" :closable="true" esc-to-close mask-closable >

        <template #header>Header one</template>

        <div class="container">
        </div>

      </a-drawer>
    </section>
</template>

<script>

export default {
  name: "StudentListView",
  props:['studentlistData','columns','operation'],
  data(){
    return{
      modalViewBoolean: false,
      drawerBoolean: false,
    }
  },methods:{
    load_Data_By_Direction(){
        this.modalViewBoolean = false
    }
  }
}
</script>

<style scoped>

</style>