<template>
  <section id="home"
           class="w-100 h-100 d-flex flex-row bg-white justify-content-center align-items-center px-5 ">
    <!--           style="background:url('https://www.icloud.com/system/icloud.com/current/static/wallpaper.webp') no-repeat center;-->
    <!--    background-size: cover;"-->


    <!--    <span style="backdrop-filter: blur(10px); top: 0; bottom: 0; left: 0;right: 0; position: absolute;"></span>-->

    <div class="">
      <div class="bg-transparent" style="height: fit-content;width: inherit; min-width: 20vw; z-index: 10000">
        <a-typography-title :heading="5" >Welcome</a-typography-title>
        <a-form :model="form"  size="large" layout="vertical" @submit="handleSubmit">
          <a-form-item  asterisk-position="end"  field="username">
            <a-input allow-clear
                     v-model="form.username"
                     placeholder="学号/邮政..."
            />
          </a-form-item>
          <a-form-item field="password" >
            <a-input-password allow-clear html-type="password" v-model="form.password" placeholder="密码..." />
          </a-form-item>
          <!--            <a-form-item field="isRead">-->
          <!--              <a-checkbox v-model="form.isRead"> I have read the manual </a-checkbox>-->
          <!--            </a-form-item>-->
          <a-form-item>
            <a-button status="normal" long type="primary" html-type="submit">Log In</a-button>
          </a-form-item>
        </a-form>
        <div class="w-100 d-flex justify-content-end py-4" >
          <a-button href="/recover" type="text" :hoverable="false"   >密码找回</a-button>
<!--          <a-button type="text" @click="visible = true"  :hoverable="false"   >注册</a-button>-->
        </div>
      </div>
    </div>

  </section>
</template>

<script>
import CapchaCodeComponent from "@/components/CapchaCodeComponent";
import {reactive, ref,} from "vue";;
import {showMessage} from "@/utils/DefinedMessageNotification";
import store from "@/store";
import { user_login} from "@/api/api";
import router from "@/router";

export default {
  name: "LoginPage",
  components: {CapchaCodeComponent},
  data(){
    return{

    }
  },mounted() {


  },
  watch:{
    visible(value){
      if (value ===true){
        this.getCode()
      }else{
        this.registerForm.email=this.registerForm.code=this.registerForm.code_verif=this.registerForm.name=this.registerForm.passport=this.registerForm.password2=this.registerForm.password=""
      }
    }
  },
  setup() {
    //LOGIN INFORMATIONS
    const form = reactive({
      username: 'admin',
      password: '123456',
      isRead: false,
    });
    const AFFDATA = ref({})
    const handleSubmit = (data) => {
      router.push({name:'supervisor_dashboard'})
        user_login(form)
            .then((response)=>{
              showMessage(response)
              if (response.code === 2000){
                store.commit('setuserInfo',response.data)
                AFFDATA.value = response.data

              }
            })
    };


    return {
      form,AFFDATA,
      handleSubmit
    };
  },
  created() {

  },
  methods:{
  }
}
</script>

