<template>
  <section>
    <MyPageHeader text="Classes Management">
      <template #icon>
        <icon-book />
      </template>
    </MyPageHeader>

    <div class="">
      <div class="main-content px-3">
        <a-tabs size="large" animation :header-padding="true" position="left" direction="vertical" type="capsule" default-active-key="1"
                lazy-load>

          <a-tab-pane  key="1">
            <template #title>
              <span class="px-3"><icon-export/> 新生 </span>
            </template>
            <div style="min-height: fit-content" class="p-2 pt-0">
              <StudentListView :operation="1" :columns="columns" :studentlist-data="data"/>
            </div>
          </a-tab-pane>

          <a-tab-pane  key="2" >
            <template #title>
              <span class="px-3"><icon-export/> 在读  </span>
            </template>
            <div style="min-height: fit-content" class="p-2 pt-0">
              <StudentListView  :operation="2" :columns="columns" :studentlist-data="data"/>
            </div>
          </a-tab-pane>

          <a-tab-pane  key="3" >
            <template #title>
              <span class="px-3"><icon-export/> 请假 </span>
            </template>
            <div style="min-height: fit-content" class="p-2 pt-0">
              <StudentListView  :operation="3" :columns="columns" :studentlist-data="data"/>
            </div>
          </a-tab-pane>

          <a-tab-pane  key="4" >
            <template #title>
              <span class="px-3"><icon-export/> 毕业 </span>
            </template>
            <div style="min-height: fit-content" class="p-2 pt-0">
              <StudentListView  :operation="4" :columns="columns" :studentlist-data="data"/>
            </div>
          </a-tab-pane>

          <a-tab-pane  key="5" >
            <template #title>
              <span class="px-3"><icon-export/> 开出 </span>
            </template>
            <div style="min-height: fit-content" class="p-2 pt-0">
              <StudentListView :operation="5" :columns="columns" :studentlist-data="data"/>
            </div>
          </a-tab-pane>
        </a-tabs>
      </div>
    </div>
  </section>
</template>

<script>
import MyPageHeader from "@/components/MyPageHeader";
import {reactive} from "vue";
import StudentListView from "@/views/Supervisor/Classes/StudentListView";
export default {
  name: "ClassesView",
  components: {StudentListView, MyPageHeader},
  setup() {
    const columns = [
      {
        title: 'Name',
        dataIndex: 'name',
        slotName: 'name'
      },
      {
        title: 'Salary',
        dataIndex: 'salary',
      },
      {
        title: 'Address',
        dataIndex: 'address',
      },
      {
        title: 'Email',
        dataIndex: 'email',
      },
      {
        title: 'Operations',
        dataIndex: 'operation',
        width: 200,
        slotName: 'operation'
      },
    ];
    const data = reactive([{
      key: '1',
      name: 'Jane Doe',
      salary: 23000,
      address: '32 Park Road, London',
      email: 'jane.doe@example.com'
    }, {
      key: '2',
      name: 'Alisa Ross',
      salary: 25000,
      address: '35 Park Road, London',
      email: 'alisa.ross@example.com'
    }, {
      key: '3',
      name: 'Kevin Sandra',
      salary: 22000,
      address: '31 Park Road, London',
      email: 'kevin.sandra@example.com'
    }, {
      key: '4',
      name: 'Ed Hellen',
      salary: 17000,
      address: '42 Park Road, London',
      email: 'ed.hellen@example.com'
    }, {
      key: '5',
      name: 'William Smith',
      salary: 27000,
      address: '62 Park Road, London',
      email: 'william.smith@example.com'
    }]);

    return {
      columns,
      data
    }
  },
}
</script>

