<template>
  <section  >
    <a-row :gutter="10" justify="start"  :style="{ width: '100%' }" :wrap="true">

      <a-col :lg="4" :span="24" :sm="12"  class="mb-2" v-for="index in size" :key="index">
        <a-card   class="card-demo rounded-1 p-0" hoverable :style="{ minHeight: '130px',
        minWidth:'fit-content'}"
                  :bordered="true">
          <a-card-meta  >
            <template #title>
              <a-space  size="large" align="center" direction="horizontal" fill>
                <span class="">数据结构与算法</span>
                <a-tag class="float-end" :color="index%2 ===0? 'orange':'green'" v-text="index%2 ===0? '未完成':'完成'"
                       size="mini"
                />
              </a-space>
            </template>
            <template #avatar>
              <a-space  align="center" fill justify="between" size="mini">
                <small class="text-muted"> <icon-check class=" bg-secondary text-white" v-if="index%2 ===0" />
                  <icon-close v-else class=" bg-danger text-white" />
                  文件已经上传了</small>
              </a-space>
            </template>
            <template #description>
              考试时间：2023/06/30
            </template>
          </a-card-meta>

          <template  #actions>
            <a-button @click="modalViewBoolean=true" v-if="index%2 ===0" size="mini" :disabled="index%2!==0"
                      type="primary" >上传成绩</a-button>
          </template>

        </a-card>
      </a-col>
    </a-row>

    <a-modal :visible="modalViewBoolean" title="数统学院2022-2023-1专业课（20、21级学生）安排"
             @ok="load_Data_By_Direction" @cancel="load_Data_By_Direction"   unmount-on-close :width="700" :closable="false"
             align-center esc-to-close mask  mask-closable
             title-align="center"   ref="modalRef" >
      <div>
        <div class="mt-3 mx-auto w-100 d-flex justify-content-center">
          <a-typography-paragraph>显示当前老师上传文件的结果（在表里面显示各个学生的成绩）<a-typography-text editable code type="">FORM
            CODE</a-typography-text></a-typography-paragraph>
        </div>
      </div>
    </a-modal>
  </section>
</template>

<script>
import MyPageHeader from "@/components/MyPageHeader";
export default {
  name: "SupGradeList",
  components: {MyPageHeader},
  props:['size'],
  data(){
    return{
      modalViewBoolean:false,
      course_list:5
    }
  },
  methods: {
    load_Data_By_Direction(){
      this.modalViewBoolean = false
    }
  },
}
</script>

